.pika-single {
  z-index: 9999;
  color: #333;
  background: #fff;
  border: 1px solid #ccc;
  border-bottom-color: #bbb;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  display: block;
  position: relative;
}

.pika-single:before, .pika-single:after {
  content: " ";
  display: table;
}

.pika-single:after {
  clear: both;
}

.pika-single.is-hidden {
  display: none;
}

.pika-single.is-bound {
  position: absolute;
  box-shadow: 0 5px 15px -5px rgba(0, 0, 0, .5);
}

.pika-lendar {
  float: left;
  width: 240px;
  margin: 8px;
}

.pika-title {
  text-align: center;
  position: relative;
}

.pika-label {
  z-index: 9999;
  background-color: #fff;
  margin: 0;
  padding: 5px 3px;
  font-size: 14px;
  font-weight: bold;
  line-height: 20px;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.pika-title select {
  cursor: pointer;
  z-index: 9998;
  opacity: 0;
  margin: 0;
  position: absolute;
  top: 5px;
  left: 0;
}

.pika-prev, .pika-next {
  cursor: pointer;
  width: 20px;
  height: 30px;
  text-indent: 20px;
  white-space: nowrap;
  opacity: .5;
  background-color: rgba(0, 0, 0, 0);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 75% 75%;
  border: none;
  outline: none;
  padding: 0;
  display: block;
  position: relative;
  overflow: hidden;
}

.pika-prev:hover, .pika-next:hover {
  opacity: 1;
}

.pika-prev, .is-rtl .pika-next {
  float: left;
  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAeCAYAAAAsEj5rAAAAUklEQVR42u3VMQoAIBADQf8Pgj+OD9hG2CtONJB2ymQkKe0HbwAP0xucDiQWARITIDEBEnMgMQ8S8+AqBIl6kKgHiXqQqAeJepBo/z38J/U0uAHlaBkBl9I4GwAAAABJRU5ErkJggg==");
}

.pika-next, .is-rtl .pika-prev {
  float: right;
  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAeCAYAAAAsEj5rAAAAU0lEQVR42u3VOwoAMAgE0dwfAnNjU26bYkBCFGwfiL9VVWoO+BJ4Gf3gtsEKKoFBNTCoCAYVwaAiGNQGMUHMkjGbgjk2mIONuXo0nC8XnCf1JXgArVIZAQh5TKYAAAAASUVORK5CYII=");
}

.pika-prev.is-disabled, .pika-next.is-disabled {
  cursor: default;
  opacity: .2;
}

.pika-select {
  display: inline-block;
}

.pika-table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  border: none;
}

.pika-table th, .pika-table td {
  width: 14.2857%;
  padding: 0;
}

.pika-table th {
  color: #999;
  text-align: center;
  font-size: 12px;
  font-weight: bold;
  line-height: 25px;
}

.pika-button {
  cursor: pointer;
  box-sizing: border-box;
  width: 100%;
  color: #666;
  text-align: right;
  height: initial;
  background: #f5f5f5;
  border: none;
  outline: none;
  margin: 0;
  padding: 5px;
  font-size: 12px;
  line-height: 15px;
  display: block;
}

.pika-week {
  color: #999;
  font-size: 11px;
}

.is-today .pika-button {
  color: #3af;
  font-weight: bold;
}

.is-selected .pika-button, .has-event .pika-button {
  color: #fff;
  background: #3af;
  border-radius: 3px;
  font-weight: bold;
  box-shadow: inset 0 1px 3px #178fe5;
}

.has-event .pika-button {
  background: #005da9;
  box-shadow: inset 0 1px 3px #0076c9;
}

.is-disabled .pika-button, .is-inrange .pika-button {
  background: #d5e9f7;
}

.is-startrange .pika-button {
  color: #fff;
  box-shadow: none;
  background: #6cb31d;
  border-radius: 3px;
}

.is-endrange .pika-button {
  color: #fff;
  box-shadow: none;
  background: #3af;
  border-radius: 3px;
}

.is-disabled .pika-button {
  pointer-events: none;
  cursor: default;
  color: #999;
  opacity: .3;
}

.is-outside-current-month .pika-button {
  color: #999;
  opacity: .3;
}

.is-selection-disabled {
  pointer-events: none;
  cursor: default;
}

.pika-button:hover, .pika-row.pick-whole-week:hover .pika-button {
  color: #fff;
  box-shadow: none;
  background: #ff8000;
  border-radius: 3px;
}

.pika-table abbr {
  cursor: help;
  border-bottom: none;
}

.pika-mcc-theme .is-today .pika-button {
  color: #000091;
}

.pika-mcc-theme .pika-button:hover, .pika-mcc-theme .pika-row.pick-whole-week:hover .pika-button {
  color: #fff;
  background-color: #000091;
}

